// –ú–æ–¥—É–ª—å —Å —Ñ—É–Ω–∫—Ü–∏—è–º–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã Drag&Drop

// –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Å—Ç–æ—Ä–æ–Ω–Ω–∏–µ –º–æ–¥—É–ª–∏
import {moveToTodo, moveToInProgress, moveToCompleted} from './cardTransfer.js';

let draggedCardId = null
// –§—É–Ω–∫—Ü–∏–∏ –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π –Ω–∞ Drag&Drop
export function addEventListenerForDragAndDrop() {
    // –î–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏–π –¥–ª—è drag and drop
    document.addEventListener('dragstart', (e) => {
      // –°–æ—Ö—Ä–∞–Ω—è–µ–º id –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–µ–º–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏
      draggedCardId = e.target.dataset.id;
      // –î–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å 'dragging' –¥–ª—è –≤–∏–∑—É–∞–ª—å–Ω–æ–≥–æ —ç—Ñ—Ñ–µ–∫—Ç–∞
      e.target.classList.add('dragging');
    });
  
    document.addEventListener('dragover', (e) => { // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏—è –Ω–∞–¥ —ç–ª–µ–º–µ–Ω—Ç–æ–º
      // –†–∞–∑—Ä–µ—à–∞–µ–º drop, —á—Ç–æ–±—ã –º–æ–∂–Ω–æ –±—ã–ª–æ –ø–æ–º–µ—Å—Ç–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É –≤ —ç—Ç–æ –º–µ—Å—Ç–æ
      e.preventDefault();
    });
  
    document.addEventListener('drop', (e) => { // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –æ—Ç–ø—É—Å–∫–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
      // –ü–æ–ª—É—á–∞–µ–º id –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞, –≤ –∫–æ—Ç–æ—Ä—ã–π –±—ã–ª–∞ –ø–µ—Ä–µ–º–µ—â–µ–Ω–∞ –∫–∞—Ä—Ç–æ—á–∫–∞
      const dropTargetId = e.target.closest('.card__container').id;
      // –ü–æ–ª—É—á–∞–µ–º —Å—Å—ã–ª–∫—É –Ω–∞ –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–µ–º—É—é –∫–∞—Ä—Ç–æ—á–∫—É –ø–æ –µ—ë id
      const card = document.querySelector(`[data-id="${draggedCardId}"]`);
  
      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –∫—É–¥–∞ –±—ã–ª–∞ –ø–µ—Ä–µ–º–µ—â–µ–Ω–∞ –∫–∞—Ä—Ç–æ—á–∫–∞, –∏ –≤—ã–∑—ã–≤–∞–µ–º —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â—É—é —Ñ—É–Ω–∫—Ü–∏—é –ø–µ—Ä–µ–º–µ—â–µ–Ω–∏—è
      if (dropTargetId === 'todo__container' && !card.classList.contains('todo__card')) { // –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ –ø—Ä–æ–≤–µ—Ä—è–µ–º, —á—Ç–æ–±—ã –Ω–µ–ª—å–∑—è –±—ã–ª–æ –ø–æ–º–µ—Å—Ç–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É –≤ –∏–∑–Ω–∞—á–∞–ª—å–Ω—ã–π —Ä–∞–∑–¥–µ–ª
        moveToTodo(card);
      } else if (dropTargetId === 'progress__container' && !card.classList.contains('progress__card')) {
        moveToInProgress(card);
      } else if (dropTargetId === 'completed__container' && !card.classList.contains('completed__card')) {
        moveToCompleted(card);
      }
  
      // –£–¥–∞–ª—è–µ–º –∫–ª–∞—Å—Å 'dragging', —á—Ç–æ–±—ã –≤–µ—Ä–Ω—É—Ç—å –∫–∞—Ä—Ç–æ—á–∫–µ –∏—Å—Ö–æ–¥–Ω—ã–π –≤–∏–¥
      card.classList.remove('dragging');
    });
  
    document.addEventListener('dragend', (e) => {
      // –£–¥–∞–ª—è–µ–º –∫–ª–∞—Å—Å 'dragging', —á—Ç–æ–±—ã –≤–µ—Ä–Ω—É—Ç—å –∫–∞—Ä—Ç–æ—á–∫–µ –∏—Å—Ö–æ–¥–Ω—ã–π –≤–∏–¥
      e.target.classList.remove('dragging');
    });
  }