// –ú–æ–¥—É–ª—å —Å –¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–º–∏ —Ñ—É–Ω–∫—Ü–∏—è–º–∏

// –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Å—Ç–æ—Ä–æ–Ω–Ω–∏–µ –º–æ–¥—É–ª–∏
import {todoContainer, completedContainer, deleteAll} from './main.js';

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è —Å–ø–∏—Å–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π —Å API 
export function getUsers () {
    fetch('https://jsonplaceholder.typicode.com/users') // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –Ω–∞ API –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è —Å–ø–∏—Å–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
    .then(response => response.json()) // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –æ—Ç–≤–µ—Ç —Å–µ—Ä–≤–µ—Ä–∞ –≤ JSON —Ñ–æ—Ä–º–∞—Ç
    .then(users => { // –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º –ø–æ–ª—É—á–µ–Ω–Ω—ã–π —Å–ø–∏—Å–æ–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
      const selector = document.querySelector('.modal__selector'); // –ù–∞—Ö–æ–¥–∏–º —Å–µ–ª–µ–∫—Ç–æ—Ä (—Å–ø–∏—Å–æ–∫ –≤—ã–±–æ—Ä–∞) –≤ DOM –ø–æ –∫–ª–∞—Å—Å—É 'modal__selector'
      while (selector.firstChild) { // –£–¥–∞–ª—è–µ–º –≤—Å–µ —Ç–µ–∫—É—â–∏–µ –æ–ø—Ü–∏–∏ –∏–∑ —Å–µ–ª–µ–∫—Ç–æ—Ä–∞
        selector.firstChild.remove();
      }
  
      // –°–æ–∑–¥–∞–µ–º —Å—Ç–∞—Ç–∏—á–µ—Å–∫—É—é –æ–ø—Ü–∏—é "Select user" –∏ –¥–æ–±–∞–≤–ª—è–µ–º –µ—ë –≤ —Å–µ–ª–µ–∫—Ç–æ—Ä
      const defaultOption = document.createElement('option');
      defaultOption.value = 0;
      defaultOption.textContent = 'Select user';
      selector.appendChild(defaultOption);
  
      users.forEach(user => { // –î–ª—è –∫–∞–∂–¥–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —Å–æ–∑–¥–∞–µ–º –Ω–æ–≤—É—é –æ–ø—Ü–∏—é –≤ —Å–µ–ª–µ–∫—Ç–æ—Ä–µ
        const option = document.createElement('option');
        option.value = user.id; // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –æ–ø—Ü–∏–∏ –∫–∞–∫ id –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
        option.textContent = user.name; // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Ç–µ–∫—Å—Ç –æ–ø—Ü–∏–∏ –∫–∞–∫ –∏–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
        selector.appendChild(option); // –î–æ–±–∞–≤–ª—è–µ–º –æ–ø—Ü–∏—é –≤ —Å–µ–ª–µ–∫—Ç–æ—Ä
      });
    });
}

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –≤–∏–¥–∏–º–æ—Å—Ç–∏ –∫–Ω–æ–ø–∫–∏ "–£–¥–∞–ª–∏—Ç—å –≤—Å–µ" –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –Ω–∞–ª–∏—á–∏—è –∑–∞–≤–µ—Ä—à–µ–Ω–Ω—ã—Ö –∑–∞–¥–∞—á
export function updateDeleteButtonVisibility() { 
    if (completedContainer.children.length === 0) { // –ï—Å–ª–∏ –≤ completedContainer –Ω–µ—Ç—É –∫–∞—Ä—Ç–æ—á–µ–∫, —Å–∫—Ä—ã–≤–∞–µ–º –∫–Ω–æ–ø–∫—É deleteAll
      deleteAll.style.display = 'none';
    } else {
      deleteAll.style.display = '';
    }
}

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –±–µ–ª–æ–π –ª–∏–Ω–∏–∏ –≤ –ø—É—Å—Ç–æ–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä TODO
export function addWhiteLine() { 
    if (todoContainer.children.length == 0) { // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–µ TODO
      todoContainer.classList.add("white-line"); // –ï—Å–ª–∏ –∫–∞—Ä—Ç–æ—á–µ–∫ –Ω–µ—Ç, –¥–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å 'white-line' –∫ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä—É
    } else {
      todoContainer.classList.remove("white-line"); // –ï—Å–ª–∏ –∫–∞—Ä—Ç–æ—á–∫–∏ –µ—Å—Ç—å, —É–¥–∞–ª—è–µ–º –∫–ª–∞—Å—Å 'white-line' –∏–∑ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞
    }
}
  