// –ú–æ–¥—É–ª—å —Å —Ñ—É–Ω–∫—Ü–∏—è–º–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å localStorage

// –§—É–Ω–∫—Ü–∏–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å localStorage: —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –∏ –ø–æ–ª—É—á–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö
export function setDate(data) { // –°—Ä–∞–∑—É —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Ñ—É–Ω–∫—Ü–∏—é
    localStorage.setItem('tasks', JSON.stringify(data)); // –°–æ—Ö—Ä–∞–Ω—è–µ–º –¥–∞–Ω–Ω—ã–µ –≤ localStorage —Å –∫–ª—é—á–æ–º 'tasks', –ø—Ä–µ–¥–≤–∞—Ä–∏—Ç–µ–ª—å–Ω–æ –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–≤ –∏—Ö –≤ —Ñ–æ—Ä–º–∞—Ç JSON
}
export function getDate() { // –°—Ä–∞–∑—É —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Ñ—É–Ω–∫—Ü–∏—é
    return JSON.parse(localStorage.getItem('tasks')); // –ü–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –∏–∑ localStorage —Å –∫–ª—é—á–æ–º 'tasks' –∏ –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –∏—Ö –∏–∑ —Ñ–æ—Ä–º–∞—Ç–∞ JSON –≤ JavaScript-–æ–±—ä–µ–∫—Ç
}