// –ú–æ–¥—É–ª—å —Å —Ñ—É–Ω–∫—Ü–∏—è–º–∏ –∑–∞–≥—Ä—É–∑–∫–∏ –∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è —Å–ø–∏—Å–∫–∞ –∑–∞–¥–∞—á

// –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Å—Ç–æ—Ä–æ–Ω–Ω–∏–µ –º–æ–¥—É–ª–∏
import {todoContainer, progressContainer, completedContainer} from './main.js'
import {getDate} from './localStorage.js';
import {updateDeleteButtonVisibility, addWhiteLine} from './additionalFunctions.js';

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –∑–∞–≥—Ä—É–∑–∫–∏ –∑–∞–¥–∞—á –∏–∑ localStorage –∏ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∏—Ö –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
export function loadTasks() { 
    const tasks = getDate(); // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—É—â–∏–π —Å–ø–∏—Å–æ–∫ –∑–∞–¥–∞—á –∏–∑ localStorage
    // –û—á–∏—â–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä—ã –∫–∞—Ä—Ç–æ—á–µ–∫ –ø–µ—Ä–µ–¥ –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ–º –Ω–æ–≤—ã—Ö
    todoContainer.innerHTML = '';
    progressContainer.innerHTML = '';
    completedContainer.innerHTML = '';
  
    tasks.forEach(task => { // –ü–µ—Ä–µ–±–∏—Ä–∞–µ–º –∫–∞–∂–¥—É—é –∑–∞–¥–∞—á—É –≤ —Å–ø–∏—Å–∫–µ
      if (task.status === 'todo') {
        todoContainer.innerHTML += createCard('todo', task); // –î–æ–±–∞–≤–ª—è–µ–º –∫–∞—Ä—Ç–æ—á–∫—É –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä TODO
      } else if (task.status === 'progress') {
        progressContainer.innerHTML += createCard('progress', task); // –î–æ–±–∞–≤–ª—è–µ–º –∫–∞—Ä—Ç–æ—á–∫—É –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä PROGRESS
      } else if (task.status === 'completed') {
        completedContainer.innerHTML += createCard('completed', task);  // –î–æ–±–∞–≤–ª—è–µ–º –∫–∞—Ä—Ç–æ—á–∫—É –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä COMPLETED
      }
    });
    updateCounts(); // –û–±–Ω–æ–≤–ª—è–µ–º —Å—á–µ—Ç—á–∏–∫–∏ –∑–∞–¥–∞—á
    // –û–±–Ω–æ–≤–ª—è–µ–º –≤–∏–¥–∏–º–æ—Å—Ç—å –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è –≤—Å–µ—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
    updateDeleteButtonVisibility();
}

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è —Å—á–µ—Ç—á–∏–∫–æ–≤ –∑–∞–¥–∞—á –≤ –∫–∞—Ç–µ–≥–æ—Ä–∏—è—Ö
export function updateCounts() { 
    // –ü–æ–ª—É—á–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—Å–µ—Ö –∑–∞–¥–∞—á
    const todoCountLength = todoContainer.children.length;
    const progressCountLength = progressContainer.children.length;
    const completedCountLength = completedContainer.children.length;
    // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—Å—Ç–æ–≤—ã–µ –ø–æ–ª—è —Å—á—ë—Ç—á–∏–∫–æ–≤
    const todoCount = document.getElementById('todo__count');
    const progressCount = document.getElementById('progress__count');
    const completedCount = document.getElementById('completed__count');
    // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –Ω—É–∂–Ω—ã–µ —Å—á—ë—Ç—á–∏–∫–∏
    todoCount.textContent = todoCountLength;
    progressCount.textContent = progressCountLength;
    completedCount.textContent = completedCountLength;
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ TODO –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ –±–µ–ª–æ–π –ª–∏–Ω–∏–∏
    addWhiteLine()
}

function createCard(status, { id, title, description, time, userName }) { // –§—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è HTML-–∫–æ–¥–∞ –∫–∞—Ä—Ç–æ—á–∫–∏ –∑–∞–¥–∞—á–∏ (–∏—Å–ø–æ–ª—å–∑—É–µ–º –¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü–∏—é)
  return `
    <div class="card ${status}__card" data-id="${id}" draggable="true">
      <div class="card__buttons">
        ${status === 'todo' ? // –ï—Å–ª–∏ —Å—Ç–∞—Ç—É—Å 'todo', –¥–æ–±–∞–≤–ª—è–µ–º –∫–Ω–æ–ø–∫–∏ Edit, Delete, Start 
          `
            <button class="card__edit todo__button">Edit</button>
            <button class="card__delete todo__button">Delete</button>
            <button class="card__forward todo__button">Start</button>
          ` :
          status === 'progress' ? // –ï—Å–ª–∏ —Å—Ç–∞—Ç—É—Å 'progress', –¥–æ–±–∞–≤–ª—è–µ–º –∫–Ω–æ–ø–∫–∏ Back, Complete 
          `
            <button class="card__back progress__button">Back</button>
            <button class="card__forward progress__button">Complete</button>
          ` :
          status === 'completed' ? // –ï—Å–ª–∏ —Å—Ç–∞—Ç—É—Å 'completed', –¥–æ–±–∞–≤–ª—è–µ–º –∫–Ω–æ–ø–∫–∏ Back, Delete
          `
            <button class="card__back completed__button">Back</button>
            <button class="card__delete completed__button">Delete</button>
          ` :
          '' // –ï—Å–ª–∏ —Å—Ç–∞—Ç—É—Å –Ω–µ —Ä–∞—Å–ø–æ–∑–Ω–∞–Ω, –Ω–µ –¥–æ–±–∞–≤–ª—è–µ–º –∫–Ω–æ–ø–∫–∏
        }
      </div>
      <h3 class="card__title">${title}</h3>
      <p class="card__description">${description}</p>
      <div class="card__bottom">
        <p class="card__user">${userName}</p>
        <p class="card__time">${time}</p>
      </div>
    </div>
  `;
}