// –ú–æ–¥—É–ª—å —Å —Ñ—É–Ω–∫—Ü–∏–µ–π –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏

// –§—É–Ω–∫—Ü–∏—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è —Ç–µ–∫—É—â–µ–≥–æ –≤—Ä–µ–º–µ–Ω–∏
export function showTime () { 
    // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—É—â—É—é –¥–∞—Ç—É –∏ –≤—Ä–µ–º—è
    const date = new Date();
    // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –≤—Ä–µ–º—è –≤ —Å—Ç—Ä–æ–∫—É
    const timeString = date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    // –ü–æ–º–µ—â–∞–µ–º –≤—Ä–µ–º—è –≤ —ç–ª–µ–º–µ–Ω—Ç —Å id clock
    document.getElementById("clock").innerText = timeString;
    // –ó–∞–ø—É—Å–∫–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é showTime –∫–∞–∂–¥—É—é —Å–µ–∫—É–Ω–¥—É
    setTimeout(showTime, 1000);
}